// ---------------Coté droite----------------------

header {
    padding: 2em 0;
}
header .name-logo {
    float: left;
}
.logo {
    color: $white !important;
    font-size: 25px;
    text-transform: uppercase;
    white-space: normal;
    font-weight: 400;
    letter-spacing: 5px;
    padding: 7px;
    display: block;
    text-align: center;
    position: relative;
    padding-left: 62px;
}
.logo-img {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center ;
}



// ---------------Coté gauche----------------------

.nav-toggle {
    cursor: pointer;
    text-decoration: none;
    &.active i {
         &::before, &::after {
             background: $black;
         }
   }
   &.dark {
       &.active i {
             &::before, &::after {
                 background: $black;
             }
       }
   }
   &:hover, &:focus, &:active {
       outline: none;
       border-bottom: none!important;
   }
   i {
         position: relative;
       display: inline-block;
       width: 40px;
       height: 2px;
       color: $black;
       font:bold 14px/.4 Helvetica;
       text-transform: uppercase;
       text-indent:-55px;
       background: $white;
       transition: all .2s ease-out;
       .menu-show & {
             background: $white;
             color: $white;
         }
         &::before, &::after {
              content:'';
            width: 40px;
            height: 2px;
            background: $white;
            position: absolute;
            left:0;
            .menu-show & {
                 background: $white;
            }
       }
   }
   &.dark {
       i {
           position: relative;
         color: $white;
         background: $white;
         transition: all .2s ease-out;
             &::before, &::after {
                background: $white;
           }
       }
   }
 }
 
 .nav-toggle i::before {
   top: -7px;
 }
 .nav-toggle i::after {
   bottom: -7px;
 }
 .nav-toggle:hover i::before {
   top: -10px;
 }
 .nav-toggle:hover i::after {
   bottom: -10px;
 }
 .nav-toggle.active i {
     background: transparent;
 }
 .nav-toggle.active i::before {
   top:0;
   -webkit-transform: rotateZ(45deg);
      -moz-transform: rotateZ(45deg);
       -ms-transform: rotateZ(45deg);
        -o-transform: rotateZ(45deg);
           transform: rotateZ(45deg);
 }
 .nav-toggle.active i::after {
   bottom:0;
   -webkit-transform: rotateZ(-45deg);
      -moz-transform: rotateZ(-45deg);
       -ms-transform: rotateZ(-45deg);
        -o-transform: rotateZ(-45deg);
           transform: rotateZ(-45deg);
 }
 .nav-toggle {
     float: right;
     z-index: 1003;
     position: relative;
     display: block;
     margin: 0 auto;
     cursor: pointer;
     margin-top: 11px;
 }



// ---------------Menu nav----------------------

#main-nav {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	background: rgba($black,.99);
	z-index: 1002;
	text-align: center;
	visibility: hidden;
	opacity: 0;
	-webkit-transition: all .8s cubic-bezier(.175, .885, .32, 1.275);
	-moz-transition: all .8s cubic-bezier(.175, .885, .32, 1.275);
	-ms-transition: all .8s cubic-bezier(.175, .885, .32, 1.275);
	-o-transition: all .8s cubic-bezier(.175, .885, .32, 1.275);
	transition: all .8s cubic-bezier(.175, .885, .32, 1.275);
	-webkit-transform: scale(0.1);
	-moz-transform: scale(0.0);
	-ms-transform: scale(0.0);
	-o-transform: scale(0.0);
	transform: scale(0.0);
	.col-table{
		display: table;
		width: 100%;
		height: 100%;
		.col-table-cell{
			display: table-cell;
			vertical-align: middle;
			position: relative;
		}
	}
	.menu-show & {
		visibility: visible;
		opacity: 1;
		-webkit-transform: scale(1.0);
		-moz-transform: scale(1.0);
		-ms-transform: scale(1.0);
		-o-transform: scale(1.0);
		transform: scale(1.0);
	}
	.logo{
		color: $white;
	}
	ul {
		padding: 0;
		margin: 0;
		display: block;
		li {
			padding: 0;
			margin: 0;
			list-style: none;
			font-weight: 400;
            font-size: 40px;
			a {
				display: block;
				color: rgba(255,255,255,1);
				padding: 5px 0;
				span{
					font-size: 40px;
					color: rgba(255,255,255,1);
					position: relative;
					padding: 0 10px;
					small{
						position: absolute;
						bottom: 15px;
						left: -30px;
						font-size: 16px;
						color: darken($white,60%);
						border-bottom: 1px solid darken($white,60%);
					}
				}
			}	
			&.active, &:active{
				a{
					span{
						color: $primary;
					}
				}
            }
            &:hover{
				a{
					transform: translate(3px, 3px);
				}
            }
		}
		
	}
}